import axios from '../utils/axios';
// 首页banner
export function banner(){
    return axios({
        url:'/wxchat/advertisSlideshow.action',
        method:'POST'
    })
}
// 首页分类
export function scope(){
    return axios({
        url:'/wxchat/getProductTypeList.action',
        method:'POST'
    })
}
// 首页消息
export function notic(){
    return axios({
        url:'/wxchat/showBulletinInfo.action',
        method:'POST'
    })
}
// 首页限时抢购
export function qiang(activityStatusType){
    return axios({
        url:'/wxchat/findTimeAndStatuByType.action',
        method:'POST',
        data:{activityStatusType:activityStatusType}
    })
}
export function qiangs(activityStatusType){
    return axios({
        url:'/wxchat/seckill.action',
        method:'POST',
        data:{activityStatusType:activityStatusType}
    })
}
// 首页活动
export function dong(){
    return axios({
        url:'/wxchat/searchActiveTypeByDictionaries.action',
        method:'POST'
    })
}
// 分类导航
export function classifyNav(id){
    return axios({
        url:'/wxchat/getDefaultType.action',
        method:'POST',
        data:{id:id}
    })
}
// 根据导航切换内容
export function classifyInner(id){
    return axios({
        url:'/wxchat/getTypeByParentId.action',
        method:'POST',
        data:{id:id}
    })
}





// 商品详情
export function shopDetails(id){
    return axios({
        url:'/wxchat/home/findProductInfo.action',
        method:'POST',
        data:{productId:id}
    })
}


// 商品列表
export function shopList(id){
    return axios({
        url:'/wxchat/searchProductListByTypeId.action',
        method:'POST',
        data:{parentId:id}
    })
}

// 专题活动页
export function listMore(id){
    return axios({
        url:'/wxchat/findProductListByactiviyId.action',
        method:'POST',
        data:{activiyId:id}
    })
}


// 搜索结果
export function search(text){
    return axios({
        url:'/wxchat/home/search.action.action',
        method:'POST',
        data:{searchText:text}
    })
}

// 订单列表
export function orderList(status){
    return axios({
        url:'/wxchat/order/searchOrderByStatu.action',
        method:'POST',
        data:{userId:371,statu:status}
    })
}


